import React from 'react';
import { CloudUrl } from '../../constants';
const defaultFinalLogo = `${CloudUrl}/images/8651481539701.51210.png`;
const BannerLogo = ({ LogoImage, image_path }) => {
    if (LogoImage.newName === 8651481539701.512 || LogoImage.newName === "8651481539701.51210") return <img src={defaultFinalLogo} className="img-responsive" style={{ maxWidth: 240, width: "auto", paddingTop: 5, paddingBottom: 5, objectFit: "contain", display: "block" }} />
    else return <img src={image_path} className="img-responsive" style={{ maxWidth: 240, width: "auto", paddingTop: 5, paddingBottom: 5, objectFit: "contain" }} />
}

export default BannerLogo;