import React from 'react'

export const PostNetworkChildIconView = ({  network, iconClass, fontAwesomeIcon, netwrk, themeRule }) => 
<div className="postNetwork" style={{ borderBottomColor : themeRule.iconType === 1 ? themeRule.iconColor : network.color }} data-network={network.name}

> 
{fontAwesomeIcon !== null && netwrk !== "google-new" && netwrk !== "giphy" ? <i className={iconClass} style={{color : themeRule.iconType === 1 ? themeRule.iconColor : network.color}}>
    {/* <FontAwesomeIcon icon={fontAwesomeIcon} /> */}
</i> :netwrk === "google-new" ? <i className={`fa fa-google`}/> : netwrk === "giphy" ? <i className={`fa fa-giphy`}/>:netwrk === "RSS"?<i className={`fa fa-rss`}/>: <i className={iconClass}></i>}</div>


