import React, { PureComponent, Fragment } from 'react';
import moment from 'moment';
import $ from "jquery";
import './error.css';
import { ERROR_BACKGROUND_IMG, ERROR_ERROR, API_ROOT_URL, V_EVENT } from '../../constants'

export default class Error extends PureComponent {
    onClickToadd = event => {
        event.preventDefault();
        let data = [];
        data['type'] = 'create_feed';
        window.parent.postMessage(data, '*');
    }

    onClickToDemoPost = event => {
        event.preventDefault();
        const { newApi } = this.props;
        let wallId = newApi.wall.Wall.id;
        let status = newApi.wall.Personalization.demoWall;
        $.ajax({
            url: `${API_ROOT_URL}display/wall/demoStatus/${wallId}/${status}`,
            cache: false,
            dataType: "json",
            timeout: (1000 * 60 * 7),
            success: function (response) {
                window.location.reload()
            },
            error: function (XMLHttpRequest, textStatus, errorThrown) {
                console.log("demoStatus error", textStatus, errorThrown)
            }

        });

    }

    onClicktoUrl = (path) => event => {
        event.preventDefault();
        window.top.location.href = path;
    }

    onClickHref = (e) => {
        e.preventDefault()

    }


    render() {
        const { err, newApi, wallErrorCode, networkHandle, olderDays } = this.props;
        let editor = window.editor ? true : window.location.pathname.includes('/editor');
        let urltagg = 'https://test.taggbox.com';
        // console.log("newApi.error_code", newApi.error_code, wallErrorCode)

        return (
            <Fragment>
                
                {
                    err === 404 ?
                        <Fragment>
                            <div className="w-100 text-center noPostSec" id="noPosts"
                                style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                <div className="noPostContent">
                                    <div className="noPostMsgBox" >
                                        <h2> <a className='text_primary' href={urltagg} target="_blank" rel="noopener noreferrer"> Wall is not active.</a></h2>
                                    </div>
                                </div>
                            </div>
                        </Fragment>

                        : newApi.error_code === 201 || wallErrorCode === 201 ?
                            <Fragment>
                                <div className={editor ? `erro_204_fix` : ``} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                    <div className='noPostContent'>
                                        <div className='noPostMsgBox text-center'>
                                            <h2 className='noPostMsgHeading'>No Posts!</h2>
                                            <p className='noPostMsgDisc'>If the Moderation is ON, approve the posts from the moderation section to make them appear on the social wall.</p>
                                            <p className='noPostMsgDisc' style={{marginBottom:"0px"}}>Else, there wont be any posts on your added feeds.</p>
                                        </div>
                                    </div>
                                </div>
                            </Fragment>

                            : newApi.error_code === 202 || wallErrorCode === 202 ?
                                <Fragment>
                                    {newApi.wall.Personalization.demoWall === 1 && editor ? 
                                        <div className='demoWallErrorStyle'>
                                        <h2 style={{ display: 'none' }}>No Feeds!</h2>
                                        <p style={{fontFamily: 'Open Sans",sans-serif', fontSize: '14px', color: '#fff', fontWeight: 300, margin: '0px'}}>Seems like you have not added any feed. Kindly add feed from your preferred social network.</p>
                                        <span style={{ padding: '0px 15px' }}><a href class="loadDemoWall" onClick={this.onClickToDemoPost}><strong>Hide Demo Posts</strong></a></span>
                                    </div>
                                    :

                                        <Fragment>
                                            <div className='w-100 text-center' style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                <div className='noPostContent'>
                                                    <div className="noPostMsgBox">
                                                        <h2 className='noPostMsgHeading'>No Feeds!</h2>
                                                        <p className='noPostMsgDisc' style={{ fontFamily: 'Open Sans",sans-serif' }}>Seems like you have not added any feed. Kindly add feed from your preferred social network.</p>
                                                        {editor ? <div style={{ margin: '20px 0 0' }}>
                                                            <a href className="btn transBtn create-new-feed addBtn"onClick={this.onClickToadd}>
                                                                Add Feed </a>
                                                            <a href className="loadDemoWall btn hideBtn showDemoPost" onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                        </div> : null}
                                                    </div>
                                                </div>
                                            </div>
                                        </Fragment>}
                                </Fragment>

                                : newApi.error_code === 203 || wallErrorCode === 203 ?
                                    <Fragment>
                                        {newApi.wall.Personalization.demoWall === 1 && editor ? 
                                            <div className='demoWallErrorStyle'>
                                            <h2 style={{ display: 'none' }}>Feed Not Activated!</h2>
                                            <p style={{ fontFamily: 'Open Sans",sans-serif', fontSize: '14px', color: '#fff',fontWeight: 300,margin: '0px'}}> Looks like your feed status is off. Click here to turn the status on! Or add another feed. <a href className="manage-feed" onClick={this.onClicktoUrl(`${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`)} style={{ cursor: 'pointer', color: '#ff4051' }}> Wall editor</a></p>
                                            <span style={{ padding: '0px 15px' }}><a href className="loadDemoWall" onClick={this.onClickToDemoPost}><strong>Hide demo posts</strong></a></span></div>
                                        :

                                            <Fragment>
                                                <div className='w-100 text-center' style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                    <div className='noPostContent'>
                                                        <div className='noPostMsgBox'>
                                                            <h2 className='noPostMsgHeading'>Feed Not Activated!</h2>
                                                            <p className='noPostMsgDisc'>Looks like your feed status is off. Click here to turn the status on! Or add another feed.
                                                            {/* <a href onClick={this.onClicktoUrl(`${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`)} className="manage-feed" style={{ cursor: 'pointer' }}> wall editor</a> */}
                                                            </p>

                                                            {editor ?<div style={{ margin: '20px 0 0' }}>
                                                                <a href className="btn transBtn create-new-feed addBtn" onClick={this.onClickToadd}>Add Another Feed </a>
                                                                <a href className="loadDemoWall btn hideBtn showDemoPost" onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                            </div> : null}
                                                        </div>
                                                    </div>
                                                </div>
                                            </Fragment>}
                                    </Fragment>

                                    : newApi.error_code === 204 || wallErrorCode === 204 ?
                                        <Fragment>
                                        <div className={editor ? `erro_204_fix` : ``} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                            <div className='noPostContent'>
                                                <div className='noPostMsgBox text-center'>
                                                    <h2 className='noPostMsgHeading'>Feed Not Authorized!</h2>
                                                    <p className='noPostMsgDisc'>It appears that you have not authorized any Feed. Please <a href={editor ? `#` : `${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`} onClick={editor ? this.onClicktoUrl(`${API_ROOT_URL}events/wall/index/${newApi.wall.Wall.id}`) : null} className="text_primary">Click here</a> to authorize now!</p>
                                                </div>
                                            </div>
                                        </div>
                                        </Fragment>

                                        : newApi.error_code === 205 || wallErrorCode === 205 ?
                                            <Fragment>
                                                {newApi.wall.Personalization.demoWall === 1 && editor ? 
                                                <div className='demoWallErrorStyle'><h2 style={{ display: 'none' }}> No Tweets/Posts!</h2>
                                                <p style={{
                                                    fontFamily: 'Open Sans",sans-serif', fontSize: '14px',
                                                    color: '#fff',
                                                    fontWeight: 300,
                                                    margin: '0px'
                                                }}>No Tweets/Posts are available on the entered <strong>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</strong>. The upcoming Tweets/posts will start appearing here.</p><span style={{ padding: '0px 15px' }}><a href class="loadDemoWall" style={{color: '#ff4051', fontWeight:"bold"}} onClick={this.onClickToDemoPost}>Hide Demo Posts</a></span></div> :
                                                    <Fragment>
                                                        <div style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                            <div className='noPostContent'>
                                                                <div className='noPostMsgBox text-center'>
                                                                    <h2 className='noPostMsgHeading'>No Tweets/Posts!</h2>
                                                                    <p className='noPostMsgDisc'>No Tweets/Posts are available on the entered <strong>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</strong>. The upcoming Tweets/posts will start appearing here.</p>
                                                                    <p class="tweets-or-section noPostMsgDisc">OR</p>
                                                                    <p className='noPostMsgDisc' style={{marginBottom:"0px"}}>you can click here to add another feed.</p>
                                                                    {editor ? <div style={{ margin: '20px 0 0' }}>
                                                                        <a href className="btn transBtn create-new-feed addBtn" onClick={this.onClickToadd}>
                                                                            Add Another Feed </a>
                                                                        <a href className="loadDemoWall btn hideBtn showDemoPost" onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                                    </div> : null}
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </Fragment>}
                                            </Fragment>

                                            : newApi.error_code === 206 || wallErrorCode === 206 ?
                                                <Fragment>
                                                    {newApi.wall.Personalization.demoWall === 1 && editor ? 
                                                        <div className='demoWallErrorStyle'>
                                                            <h2 style={{ display: 'none' }}>No Tweets!</h2>
                                                            <p style={{
                                                                fontFamily: 'Open Sans",sans-serif', fontSize: '14px',
                                                                color: '#fff',
                                                                fontWeight: 300,
                                                                margin: '0px'
                                                            }}>
                                                                No Tweets are available on the entered <strong>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</strong>. All the upcoming tweets will appear here.
                                                            </p>
                                                            <span style={{ padding: '0px 15px' }}><a href class="loadDemoWall" onClick={this.onClickToDemoPost}><strong>Hide Demo Posts</strong></a></span>
                                                        </div>
                                                    :

                                                        <Fragment>
                                                            <div className='w-100 text-center' style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                                <div className='noPostContent'>
                                                                    <div className='noPostMsgBox'>
                                                                        <h2 className='noPostMsgHeading'>No Tweets!</h2>
                                                                        <p className='noPostMsgDisc' style={{width:"90%"}}>No Tweets are available on the entered <strong>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</strong>. All the upcoming tweets will appear here.</p>
                                                                        <p className="tweets-or-section noPostMsgDisc">OR</p>
                                                                        <p className='noPostMsgDisc' style={{marginBottom:"0px"}}>The Tweets are older than your chosen {olderDays===2 ? `7` : `30`} days.</p>
                                                                        {editor ? <div style={{ margin: '20px 0 0' }}>
                                                                            <a href className="btn transBtn create-new-feed addBtn" onClick={this.onClickToadd}>
                                                                                Add Another Feed </a>
                                                                            <a href className="loadDemoWall btn hideBtn showDemoPost" onClick={this.onClickToDemoPost}>SHOW DEMO POSTS</a>
                                                                        </div>: null}
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </Fragment>}
                                                </Fragment>

                                                : newApi.error_code === 207 || wallErrorCode === 207 ?
                                                    <Fragment>
                                                        <div className={editor ? `erro_204_fix w-100 text-center` : `w-100 text-center`} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                            <div className='noPostContent'>
                                                                <div className="noPostMsgBox">
                                                                    <h2 className='noPostMsgHeading'>No Posts!</h2>
                                                                    <p className='noPostMsgDisc' style={{ fontFamily: 'Open Sans",sans-serif' }}>It appears that all your posts are private. Please go to <a href={`app.taggbox.com/moderation/index/${newApi.wall.Wall.id}`} target="_blank" rel="noopener noreferrer" className='text_primary'>Moderation</a> and make some posts public.</p>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </Fragment>

                                                    : newApi.error_code === 208 || wallErrorCode === 208 ?
                                                        <Fragment>
                                                            <div className={editor ? `erro_204_fix w-100 text-center` : `w-100 text-center`} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                                <div className='noPostContent'>
                                                                    <div className="noPostMsgBox">
                                                                        <h2 className='noPostMsgHeading'>No Posts!</h2>
                                                                        <p className='noPostMsgDisc' style={{ fontFamily: 'Open Sans",sans-serif' }}>It appears that the available posts are older than your chosen {networkHandle !== 8 ? `${networkHandle} Days` : moment(new Date(newApi.older_post * 1000)).format('LL')}
                                                                    {'. '} All the new posts will start appearing here.</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </Fragment>

                                                        : newApi.error_code === 209 || wallErrorCode === 209 ?
                                                            <Fragment>
                                                                <div className={editor ? `erro_204_fix w-100 text-center` : `w-100 text-center`} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                                    <div className='noPostContent'>
                                                                        <div className='noPostMsgBox text-center'>
                                                                            <h2 className='noPostMsgHeading'>No Posts!</h2>
                                                                            <p className='noPostMsgDisc'>Looks like there are no posts available with images or videos. The theme doesn't support text content.</p>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </Fragment>

                                                            : newApi.error_code === 211 || wallErrorCode === 211 ?
                                                        <Fragment>
                                                           <div className={editor ? `erro_204_fix w-100 text-center` : `w-100 text-center`} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                                <div className='noPostContent'>
                                                                    <div className='noPostMsgBox text-center'>
                                                                        <h2 className='noPostMsgHeading'>No Posts!</h2>
                                                                        <p className='noPostMsgDisc'>No posts are available on the entered <strong>{ERROR_ERROR[`${olderDays}`]}{networkHandle}</strong>. All the upcoming posts will appear here.</p>
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </Fragment>
                                                            : newApi.error_code === 212 || wallErrorCode === 212 ? V_EVENT ? 
                                                                <div className={editor ? `erro_204_fix w-100 text-center` : `w-100 text-center`} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                                    <div className='noPostContent'>
                                                                        <div className='noPostMsgBox text-center'>
                                                                            <h2 className='noPostMsgHeading'>The Applied Theme is Not Compatible with Virtual Events.</h2>
                                                                        </div>
                                                                    </div>
                                                                </div> : <div className={editor ? `erro_204_fix w-100 text-center` : `w-100 text-center`} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                                    <div className='noPostContent'>
                                                                        <div className='noPostMsgBox text-center'>
                                                                            <h2 className='noPostMsgHeading'>No Posts!</h2>
                                                                            <p className='noPostMsgDisc'>Only new posts from Instagram {ERROR_ERROR[`${olderDays}`]}{networkHandle} will appear here. Existing posts from Instagram {ERROR_ERROR[`${olderDays}`]}{networkHandle} can not be retrieved.</p>
                                                                        </div>
                                                                    </div>
                                                                </div> : newApi.error_code === 210 || wallErrorCode === 210 ? 
                                                                <div className={editor ? `erro_204_fix w-100 text-center` : `w-100 text-center`} style={{ backgroundImage: 'url(' + ERROR_BACKGROUND_IMG + ')' }}>
                                                                    <div className='noPostContent'>
                                                                        <div className='noPostMsgBox text-center'>
                                                                            <h2 className='noPostMsgHeading'>Social wall is not active.</h2>
                                                                        </div>
                                                                    </div>
                                                            </div>: null}
                                                        </Fragment>)
    }

}
