import React from 'react'
import Iframe from 'react-iframe';
import ReactPlayer from 'react-player'
import Vimeo from '@u-wave/react-vimeo';
import { IMG_LOAD_AFTER_COMPLETE_LAYOUT, IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP } from '../../utils'
import { NO_CAMERA_IMAGE } from '../../constants'
import AudioPlayer from 'react-h5-audio-player';
import 'react-h5-audio-player/lib/styles.css';


const VideoImageRender = ({ person, videoChange, newApi }) => {
  // console.log("newApi, person", newApi, person );
  if ((person.type === 2) || (person.type === 4)) return <div className="tb_media_holder">
    <div className="tb_blur_bg" style={{ backgroundImage: 'url(' + person.filename + ')' }}></div>
    <div className="tb_img_holder">
      <img className="tb-detail-image" src={person.filename} alt="" height="1000" width="1000"
        data-load="0" data-network={person.networkId} data-postid={person.id}
        data-wall-id={newApi.Wall.id} data-item-id={person.id}
        data-tb={newApi.UserDetail.db_table}
        data-filter-id={person.filterId}
        data-stories={person.stories}
        onError={(e) => {
          e.target.src = NO_CAMERA_IMAGE;
          IMG_LOAD_AFTER_COMPLETE_LAYOUT(e)

        }} />
    </div>
  </div>

  else if (person.type === 1) return <div className="tb_blur_bg" ></div>
  // else if ((person.type === 3) || (person.type === 5) && person.link.includes("instagram")) return <div className="tb_media_holder">
  //   <div className="tb_blur_bg" style={{ backgroundImage: 'url(' + person.filename + ')' }}></div>
  //   <div className="tb_img_holder">
  //     <img className="tb-detail-image" src={person.filename} alt="" height="1000" width="1000" />
  //   </div>
  // </div>
  else if ((person.type === 3) || (person.type === 5)) {

    if (person.link.indexOf("facebook") >= 0) return <ReactPlayer onEnded={videoChange} className="tb_vid_player" url={`${person.mediaUrl}&mute=1`} height='100%' width='100%' controls={true} allowFullScreen={true} playing style={{ backgroundImage: 'url(' + person.filename + ')' }} />
    else if (person.link.indexOf("vimeo") >= 0) return <Vimeo onEnd={videoChange} className="tb_vimeo_vid00" video={`${person.mediaUrl}&mute=1`} autoplay />
    else if ((person.networkId && person.networkId == 30) && person.link.indexOf("vk") >= 0) return <Iframe className="iframeVideo youtubeVideoPlayer init" url={`${person.mediaUrl}&autoplay=1&mute=1`} width="100%" height="400" allowFullScreen={true}></Iframe>
    else if (person.link.indexOf("youtube") >= 0 || person.link.indexOf("youtu.be") >= 0 || person.link.indexOf("soundcloud") >= 0 ||
      person.link.indexOf("tumblr") >= 0 || person.link.indexOf("linkedin") >= 0 || person.link.indexOf("workplace") >= 0) return <Iframe className="iframeVideo youtubeVideoPlayer init" url={`${person.mediaUrl}&mute=1`} width="100%" height="400" allowFullScreen={true}>
      </Iframe>
    // else if (person.link.indexOf("instagram") >= 0) return <div className="tb_media_holder">
    //   <div className="tb_blur_bg" style={{ backgroundImage: 'url(' + person.filename + ')' }}></div>
    //   <div className="tb_img_holder">
    //     <img className="tb-detail-image" src={person.filename} alt="" height="1000" width="1000" />
    //   </div>
    // </div>
    else if (person && person.isAudio) return <div className="tb_post_modal_media_holder">
        <div className="tb_post_modal_audio_thumb_blur" style={{ backgroundImage: `url(${person.postFileNew})`}}></div>
        <div className="tb_post_modal_audio_thumb" style={{ backgroundImage: `url(${person.postFileNew})`}}></div>
        <div className="tb_post_modal_audio_wrapper"> 
            <AudioPlayer
                className="tb_post_modal_audio"
                autoPlay={true}
                showJumpControls={false}
                showDownloadProgress={false}
                showFilledProgress={false}
                hasDefaultKeyBindings={false}
                layout='horizontal-reverse'
                customVolumeControls={[]}
                customAdditionalControls={[]}
                src={person.mediaUrl}
                />
        </div> 
    </div>
    else return <video onEnded={videoChange} playsInline className="iframeVideo youtubeVideoPlayer init" src={person.mediaUrl} autoPlay="autoPlay" muted onError={(e) => { IMG_LOAD_AFTER_COMPLETE_LAYOUT_FOR_POP_UP(e) }}
      data-filter-id={person.filterId}
      data-type="video"
      data-stories={person.stories}
      data-network={person.networkId}
      data-postid={person.id}
      data-link={person.link}
      data-load={0}
      data-tb={newApi.UserDetail.db_table}
      data-wall-id={newApi.Wall.id} data-item-id={person.id}

    ></video>
  }

}

export default VideoImageRender
